body {
  margin: 0 auto;
}

.header {
  background: linear-gradient(90deg, #090979 0%, #00d4ff 100%);
  justify-content: center;
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
}

.search-form input {
  color: #090979;
  width: 400px;
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 7px;
  padding: 7px;
  font-size: 16px;
}

.search-form button {
  width: 35px;
  height: 35px;
  cursor: pointer;
  color: #090979;
  border: 1px;
  border-radius: 50%;
  margin-left: 20px;
}

.search-form input:hover, .search-form input:focus, .search-form button:hover, .search-form button:focus {
  cursor: pointer;
  border: solid #090979;
}

.gallery {
  flex-wrap: wrap;
  align-items: center;
  gap: 10px;
  padding: 10px;
  display: flex;
}

.photo-card {
  height: 320px;
  max-width: max-content;
  border-radius: 0 0 4px 4px;
  flex-grow: 1;
  flex-basis: calc(25% - 7.5px);
  align-self: center;
  display: block;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.img__wrapper > div {
  width: 100%;
  height: 250px;
}

.photo-card > a {
  text-decoration: none;
  display: block;
}

.img__wrapper > img {
  width: 100%;
  height: 250px;
  object-fit: contain;
  display: block;
  overflow: hidden;
}

.info {
  color: #090979;
  text-align: center;
  justify-content: space-around;
  align-items: center;
  gap: 5px;
  padding: 2px 5px;
  display: flex;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #090979;
  border: none;
  border-radius: 5px;
  margin: 30px auto;
  padding: 10px;
  font-size: 18px;
  display: block;
}

.load-more:hover, .load-more:focus {
  background-color: #00d4ff;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  scale: 0;
}

.load-more:disabled {
  cursor: auto;
  background-color: #00d4ff;
}

